<template>
    <div id="container">
      <div class="tit">{{data.title}}</div>
      <div class="price">{{ data.price }}원</div>
      <div class="img">
        <img :src="data.image" :alt="data.id">
      </div>
      <div class="txt">{{data.text}}</div>
    </div>
</template>
<script>
import coffee from '@/assets/coffee'
export default {
  name: 'Detail',
  data() {
    const index = this.$route.params.contentId;
      return {
        data: coffee[index],
      }
  },
  methods: {

  },
  mounted() {

  },
}
</script>
<style scoped>
tit {
    font-size: 3rem;
    font-weight: 600;
    position: relative;
    color: rgb(107, 51, 160)
}

.tit::after {
    content: "";
    display: block;
    width: 10%;
    margin: 2rem auto;
    height: .3rem;
    background-color: #ccc
}

.price {
    font-size: 2rem;
}

.img {
    border: 1px solid #ddd;
    /* display: inline-block; */
    padding: 5rem;
    max-width: 60%;
    margin: 3rem auto
}

.img img {
    width: 100%;
}

.txt {
    font-size: 1.6rem;
    margin-top: 3rem;
    color: #5c5c5c;
    text-indent: 2rem;
    /* text-align: ; */
    line-height: 1.8
}
</style>


