<template>
  <div id="container">
    <h2>coffee list</h2>
    <div class="in_wrap">
      <div class="content" v-for="(item, idx) in coffee" :key="idx" @click="detail(idx)">
        <h3>{{item.title}}</h3>
        <img :src="item.image" alt="">
      </div>
    </div>
    <div class="more" @click="more">더보기</div>
  </div>
</template>
<script>
import axios from 'axios';
import coffee from '@/assets/coffee';
export default {
  data() {
    return {
      coffee: coffee,
      moreList: 0,
    }
  },
  methods: {
    detail(idx) {
      this.$router.push({
        name: 'Detail',
        params: {
          contentId: idx,
        }
      })
    },
    more() {
      axios.get(`https://joonsol.github.io/data/more${this.moreList}.json`)
      .then(result => {
        console.log(result);
        this.coffee.push(result.data);
        this.moreList++;
        if (this.moreList == 2) {
          this.moreList = 0;
        }
      })
    },
  },
}
</script>
<style scoped>
.more {
  border: none;
  color:#fff;
  border-radius: 2rem;
  padding: 0 5rem;
  display: inline-block;
  margin-top: 3rem;
  line-height: 4rem;
  height: 4rem;
  cursor: pointer;
  background-color: rgb(107, 51, 160);
}
</style>


