<template>
  <div id="container">
    <h1>Todays Reports</h1>
    <img :src="this.icon" alt="">
    <h2>
      <span>{{city}}</span>'s Weather<br>
      <b>{{dsc}}</b>
    </h2>
    <p class="temp">
      temp : {{temp}} <span>&#176;</span>
    </p>
    <form action="" @submit.prevent="weatherData">
      <h3><b>도시</b>를 입력하세요</h3>
      <input type="text" placeholder="City Name" v-model="city">
    </form>
  </div>
</template>
<script>
import axios from 'axios';
import background from '@/assets/background';
export default {
  data() {
    return {
      bg: background,
      icon: '',
      dsc: '',
      temp: '',
      city: 'seoul',
      mainDsc: '',
    }
  },
  methods: {
    weatherData() {
      axios.get(`https://api.openweathermap.org/data/2.5/weather?q=${this.city}&appid=209427778b168dd3cfb8c77b931a8344&units=metric`)
      .then((res) => {
        let data = res.data;
        this.icon = `https://openweathermap.org/img/wn/${data.weather[0].icon}@2x.png`;
        this.dsc = data.weather[0].description;
        this.temp = data.main.temp;
        this.mainDsc = data.weather[0].main;

        const container = document.querySelector('#container');
        for(let i=0; i<this.bg.length; i++) {
          if(this.mainDsc == this.bg[i].main) {
            container.style.background = this.bg[i].background;
          }
        console.log(this.mainDsc);
        }
      })
      .catch((err) => {
        console.log(err);
      })
    }
  },
  mounted() {
    this.weatherData();
  },
}
</script>
<style scoped>
@import url(//spoqa.github.io/spoqa-han-sans/css/SpoqaHanSansNeo.css);
* {
    font-family: 'Spoqa Han Sans Neo', 'sans-serif';
}
  #container {
    text-align: center;
    color: #fff;
    background: rgb(37, 46, 148);
    background: radial-gradient(circle,
        rgba(37, 46, 148, 1) 0%,
        rgba(0, 0, 0, 1) 100%);
    min-height: 90vh;
    text-align: center;
    padding: 25vh 0;
    position: relative;
  }
  h1 {
    font-size: 40px;
    font-weight: 600;
  }
  form {
    bottom: 0;
    position: absolute;
    width: 100%;
    border-radius: 40px 40px 0 0;
    background-color: #fff;
    box-shadow: 0 -20px 20px rgba(0, 0, 0, 0.144);
    color: #000;
    padding: 50px 0;
  }
  h2 {
    font-weight: 200;
    font-size: 30px;
    background: initial;
  }
  h3 {
    font-weight: 200;
    font-size: 20px;
    color: initial;
  }
  h3:after {
    display: none;
  }
  span {
    font-weight: 400;
  }
  input {
    border-left: none;
    border-right: none;
    border-top: none;
    display: inline-block;
    line-height: 1.6;
    font-size: 20px;
    padding-top: 20px;
    text-align: center;
    outline: none;
  }
  img {
    width: 100px;
  }
</style>


